import { DecimalPipe } from '@angular/common';
import { HttpErrorResponse, HttpStatusCode } from '@angular/common/http';
import { ChangeDetectorRef, Component, HostListener, OnInit } from '@angular/core';
import { MAX_AI_TEXT_SCAN_NUM_OF_CHARS, MIN_AI_TEXT_SCAN_NUM_OF_CHARS } from '@ngx-common/constants/ai-scan.consts';
import { EAPIErrorCodes } from '@ngx-common/enums/error-codes.enum';
import { ELocalStorageKeys } from '@ngx-common/enums/local-storage-keys.enum';
import { EMatchClassification, IAIScanHttpResult } from '@ngx-common/models/ai-scan-result';
import { IAPICustomErrors } from '@ngx-common/models/api-custom-errors.model';
import { HumanFeedback } from '@ngx-common/models/human-feedback';
import { AiTextSelectionPipe } from '@ngx-common/pipes/ai-text-selection-pipe/ai-text-selection.pipe';
import { LocalStorageService } from '@ngx-common/services/localstorage.service';
import { isWithinXMilliseconds } from '@ngx-common/utils/date-time.utils';
import { jsc } from '@ngx-common/utils/jsc.utils';
import { IExtUser } from '@shared/interfaces';
import { ExtAuthService } from '../../services/ext-auth.service';
import { ScanPageService } from './services/scan-page.service';
import { ExtPaymentService } from '../../services/ext-payment.service';
import { Router } from '@angular/router';
import { EPagesPath } from '../../enums/pages-path.enum';
import { IUserBalance } from '@ngx-common/models/user-balance.models';
import { EPaymentPlanType } from '@ngx-common/enums/payment-plan-type.enum';
import { EAiSubscriptionRequirementTypes } from '../../enums/subscription-requirment-types.enum';
@Component({
	selector: 'app-scan-page',
	templateUrl: './scan-page.component.html',
	styleUrls: ['./scan-page.component.scss'],
})
export class ScanPageComponent implements OnInit {
	toggleMenu: boolean;
	user: IExtUser;
	errMsg: string;
	userBalance: IUserBalance;
	isOrganizationMember: boolean;
	isScanning = false;
	showReviewMessage = false;
	scanResult: IAIScanHttpResult;
	totalAIWords: 0;
	inputText = '';
	isLiked: boolean;
	showRateUsSection: boolean;
	subscriptionRequirmentType: EAiSubscriptionRequirementTypes;
	isLoading: boolean;

	// tooltip
	isTooltipShown = false;
	aiMatchTooltipTop = '0px';
	aiMatchTooltipLeft = '0px';
	aiMatchTooltipProbability: number;
	aiMatchProbabilityStyle: number;
	aiMatchTooltipWords: number;
	aiMatchTooltipClassification: EMatchClassification;
	eMatchClassification = EMatchClassification;
	// endof tooltip

	get isHuman(): boolean {
		if (this.scanResult) {
			return this.scanResult.summary.ai == 0;
		}
		return false;
	}

	constructor(
		private _decimalPipe: DecimalPipe,
		private _authService: ExtAuthService,
		private _router: Router,
		private _paymentService: ExtPaymentService,
		private _changeDetectorRef: ChangeDetectorRef,
		private _scanPageSvc: ScanPageService,
		private _localStorageService: LocalStorageService,
		private _aiTextSelectionPipe: AiTextSelectionPipe
	) {}

	@HostListener('mousemove', ['$event'])
	onMousemove($event: MouseEvent) {
		const target: HTMLSpanElement = $event?.target as HTMLSpanElement;
		if (target?.nodeName == 'SPAN') {
			// if (!this.isTooltipShown) {
			this.aiMatchTooltipWords = +target.attributes.getNamedItem('cl-scan-words')?.value;
			this.aiMatchTooltipProbability = +target.attributes.getNamedItem('cl-scan-probability')?.value;
			this.aiMatchProbabilityStyle = this._aiTextSelectionPipe.getProbabilityMatchStyle(this.aiMatchTooltipProbability);
			this.aiMatchTooltipClassification = +target.attributes.getNamedItem('cl-scan-classification')?.value;

			if (this.aiMatchTooltipWords && this.aiMatchTooltipProbability) {
				let positionX = $event.pageX;
				const positionY = $event.pageY;

				if (positionX > 300) {
					positionX = 300;
					if (this.aiMatchTooltipClassification == EMatchClassification.AI) {
						positionX = 340;
					}
				}

				this.aiMatchTooltipLeft = positionX + 'px';
				this.aiMatchTooltipTop = positionY + 'px';

				if (this.aiMatchTooltipProbability > 0.9994) {
					this.aiMatchTooltipProbability = 0.9994;
				}
				this.isTooltipShown = true;
				this._changeDetectorRef.detectChanges();
			} else {
				this.isTooltipShown = false;
			}
			// }
		} else {
			if (this.isTooltipShown) {
				this.isTooltipShown = false;
				this._changeDetectorRef.detectChanges();
			}
		}
	}

	async ngOnInit() {
		this.isLoading = true;
		this.userBalance = await this._paymentService.getExtensionUserBalanceAsync();
		this.isOrganizationMember = this.userBalance.accountType == EPaymentPlanType.SharedAccount;
		if (!this.userBalance.features.aiDetectorEnabled) {
			this._router.navigate([`/${EPagesPath.ExtSubscriptionRequirement}`], {
				queryParams: {
					reason: EAiSubscriptionRequirementTypes.AiSubscriptionMissing,
				},
			});
		}
		var extensionInstallationDate = this._localStorageService.getItem(ELocalStorageKeys.ExtensionFirstUse);
		if (!extensionInstallationDate) {
			var dateNow = new Date();
			this._localStorageService.setItem(ELocalStorageKeys.ExtensionFirstUse, dateNow);
			this._localStorageService.setItem(ELocalStorageKeys.ExtensionCanShowReview, true);
		}
		this.user = this._authService.user;
		chrome.tabs.query({ active: true, currentWindow: true }, async tabs => {
			var selectedItems: chrome.scripting.InjectionResult<string>[] = await chrome.scripting.executeScript({
				target: { tabId: tabs[0].id! },
				func: getSelectedText,
			});

			if (selectedItems.length) {
				var selectedText = selectedItems[0].result;
				this.inputText = selectedText;
				var trimmedText = selectedText.trim();
				if (trimmedText) {
					this.ScanAsyc();
				} else {
					this.inputText = '';
				}
				this._changeDetectorRef.detectChanges();
			}
		});
		this.isLoading = false;
	}

	clear() {
		this.errMsg = '';
		this.isScanning = false;
		this.scanResult = null;
		this.totalAIWords = 0;
		this.inputText = '';
		this.isLiked = null;
		this.showRateUsSection = false;
	}

	async ScanAsyc() {
		try {
			this.isScanning = true;
			this.errMsg = null;

			if (!this.inputText) {
				this.errMsg = 'Theres nothing to scan here...';
				return;
			}

			// check if the submitted text validate the required text length for the scan
			if (this.inputText.length < MIN_AI_TEXT_SCAN_NUM_OF_CHARS) {
				this.errMsg = `Not enough text to scan. The minimum text length is ${MIN_AI_TEXT_SCAN_NUM_OF_CHARS} characters.`;
				return;
			}

			if (this.inputText.length > MAX_AI_TEXT_SCAN_NUM_OF_CHARS) {
				this.errMsg = `Too long, The maximum number of characters allowed is ${this._decimalPipe.transform(
					MAX_AI_TEXT_SCAN_NUM_OF_CHARS
				)}.`;
				return;
			}

			// prev scan text
			const pst = this._localStorageService.getItem(ELocalStorageKeys.PT, true);
			// prev scan date
			const psd = this._localStorageService.getItem(ELocalStorageKeys.PD, true);
			// prev scan id
			let psi: string;
			// Jaccard similarity Check result
			let sim: number;

			if (psd && isWithinXMilliseconds(new Date(+psd))) {
				sim = jsc(this.inputText, pst);
				if (sim != 1 && sim != 0) {
					psi = this._localStorageService.getItem(ELocalStorageKeys.PI, true);
				}
			}

			this.scanResult = await this._scanPageSvc.scanTextAsync(this.inputText, psi, sim);

			this._onScanResultChangeAsync();
			this._paymentService.getExtensionUserBalanceAsync();
		} catch (error) {
			const _ex = error as HttpErrorResponse;
			const errorBody = _ex.error as IAPICustomErrors;
			if (_ex.status == HttpStatusCode.BadRequest && errorBody.code) {
				switch (errorBody.code) {
					case EAPIErrorCodes.UnsupportedLang:
						this.errMsg = 'Unsupported language.';
						return;
					case EAPIErrorCodes.TooShort:
						this.errMsg = 'Insufficient text';
						return;
					default:
						break;
				}
			}
			if (_ex.status == HttpStatusCode.TooManyRequests) {
				this.errMsg = 'Too many requests';
			}
			this.errMsg = 'something went wrong, please try again later';
		} finally {
			this.isScanning = false;
			this._changeDetectorRef.detectChanges();
		}
	}

	private async _onScanResultChangeAsync() {
		this.totalAIWords = 0;
		if (this.scanResult) {
			if (!this.isHuman) {
				this.scanResult.results
					.filter(e => e.classification == EMatchClassification.AI)
					.forEach(result => {
						result.matches.forEach(match => {
							this.totalAIWords += match.text.words.lengths[0];
						});
					});
			}

			// update prev
			this._localStorageService.setItem(ELocalStorageKeys.PT, this.inputText, true);
			this._localStorageService.setItem(ELocalStorageKeys.PI, this.scanResult.scannedDocument.scanId, true);
			this._localStorageService.setItem(ELocalStorageKeys.PD, Date.now(), true);
			// An integer that keeps tracks of how many scans the user did.
			var scansCount = this._localStorageService.getIntegerParsedItem(ELocalStorageKeys.ExtensionScanCount);
			scansCount += 1;
			this._localStorageService.setItem(ELocalStorageKeys.ExtensionScanCount, scansCount);
			this.checkIfShowReview(scansCount);
		}
	}

	onLike(isOn: boolean) {
		if (isOn) {
			this.isLiked = true;
			if (this._localStorageService.getItem(ELocalStorageKeys.ExtensionRateUsTriggered, true)) {
				this.showRateUsSection = false;
			} else {
				this.showRateUsSection = true;
			}
			this._scanPageSvc.humanFeedbackAsync(
				this.scanResult.scannedDocument.scanId,
				new HumanFeedback(this.scanResult).getFeedback(true)
			);
		} else {
			this.isLiked = null;
		}
	}
	openRateUsLink() {
		this._localStorageService.setItem(ELocalStorageKeys.ExtensionRateUsTriggered, true, true);
		chrome.tabs.create({
			url: 'https://chrome.google.com/webstore/detail/ai-content-detector-copyl/gplcmncpklkdjiccbknjjkoidpgkcakd',
		});
	}

	async dislikeAsync(isOn: boolean) {
		if (isOn) {
			this.isLiked = false;
			this._scanPageSvc.humanFeedbackAsync(
				this.scanResult.scannedDocument.scanId,
				new HumanFeedback(this.scanResult).getFeedback(false)
			);
		} else {
			this.isLiked = null;
		}
	}

	checkIfShowReview(scanCount: number) {
		// This is a boolean that defines if the user can be shown the review message, this is always true until the user leaves a review or cancels 3 times.
		var canShow = this._localStorageService.getItem(ELocalStorageKeys.ExtensionCanShowReview);
		if (!canShow || canShow == 'false') {
			return;
		}
		// The current date in milliseconds
		var currentDate = Date.now();
		var latestSavedDateAsString: string;
		var numberOfDaysDifferenceToShowReview: number;
		// The amount of times the user canceled the review request
		var reviewCancelCount = this._localStorageService.getIntegerParsedItem(
			ELocalStorageKeys.ExtensionReviewCancellationCount
		);
		if (!reviewCancelCount) {
			latestSavedDateAsString = this._localStorageService.getItem(ELocalStorageKeys.ExtensionFirstUse);
			numberOfDaysDifferenceToShowReview = 5;
		} else if (reviewCancelCount < 3) {
			latestSavedDateAsString = this._localStorageService.getItem(ELocalStorageKeys.ExtensionReviewCanceledDate);
			numberOfDaysDifferenceToShowReview = 10;
		}
		// Logic to check if the review can be shown
		if (latestSavedDateAsString) {
			var latestSavedDateInMilliSeconds = Date.parse(latestSavedDateAsString);
			let differenceInTime = currentDate - latestSavedDateInMilliSeconds;
			var numberOfDaysFromLastDate = Math.round(differenceInTime / (1000 * 3600 * 24));
			if (numberOfDaysFromLastDate >= numberOfDaysDifferenceToShowReview) {
				var requiredScanCount = 20 + reviewCancelCount * 10;
				if (scanCount && scanCount >= requiredScanCount && this.isHuman) {
					this.showReviewMessage = true;
				}
			}
		}
	}

	cancelReview() {
		this.showReviewMessage = false;
	}
}

// chrome helpers.
const getSelectedText = () => window.getSelection()?.toString() ?? '';
