import { EHumanFeedback } from '../enums/human-feedback.enum';
import { IAIScanHttpResult } from './ai-scan-result';

export class HumanFeedback {
	public scanResult: IAIScanHttpResult;
	constructor(scanResult: IAIScanHttpResult) {
		this.scanResult = scanResult;
	}
	getFeedback(isLiked: boolean) {
		if (isLiked) {
			if (this.scanResult.summary.ai == 1) {
				return EHumanFeedback.PositiveAi;
			} else if (this.scanResult.summary.human == 1) {
				return EHumanFeedback.PositiveHuman;
			} else {
				return EHumanFeedback.PositiveMixed;
			}
		} else {
			if (this.scanResult.summary.ai == 1) {
				return EHumanFeedback.NegativeAi;
			} else if (this.scanResult.summary.human == 1) {
				return EHumanFeedback.NegativeHuman;
			} else {
				return EHumanFeedback.NegativeMixed;
			}
		}
	}
}
