/**
 *
 * @param date Checked Date
 * @param x milliseconds - default to 5 mins (5 * 60 * 1000)
 * @returns
 */
export function isWithinXMilliseconds(date: Date, x: number = 5 * 60 * 1000) {
	const now = new Date();
	const difference = now.getTime() - date.getTime();
	return difference < x;
}

export function SecToHHMMSSClockString(secounds: number) {
	const arr = SecToHHMMSSArray(secounds);

	if (arr[0] === 0) {
		arr.shift();
	}

	return arr
		.map(v => (v < 10 ? '0' + v : v))
		.filter((v, i) => v !== '00' || i > 0)
		.join(':');
}
export function SecToHHMMSSArray(secounds) {
	const hours = Math.floor(secounds / 3600);
	const minutes = Math.floor(secounds / 60) % 60;
	const seconds = Math.floor(secounds % 60);

	return [hours, minutes, seconds];
}

export function GetSecoundsBetweenTowDates(startDate: Date, endDate: Date) {
	return (endDate.getTime() - startDate.getTime()) / 1000;
}
