import { Pipe, PipeTransform } from '@angular/core';
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';
import { EMatchClassification, IAIScanHttpResult, IAIScanResultMatch } from '../../models/ai-scan-result';

@Pipe({
	name: 'aiTextSelection',
})
export class AiTextSelectionPipe implements PipeTransform {
	constructor(private sanitized: DomSanitizer) {}

	transform(html: string, scanResult: IAIScanHttpResult): SafeHtml {
		const resultHTML = this.transformToString(html, scanResult);

		return this.sanitized.bypassSecurityTrustHtml(resultHTML || this.prepareTextForInjection(html));
	}

	transformToString(html: string, scanResult: IAIScanHttpResult): string {
		var charMatches: IMatch[] = [];
		scanResult?.results?.forEach(result => {
			result?.matches?.forEach((match: IAIScanResultMatch) => {
				const mappedMatches = match?.text.chars?.starts?.map(
					(start, idx) =>
						({
							start,
							end: start + match.text.chars.lengths[idx],
							classification: result.classification,
							probability: result.probability,
							totalWords: match.text.words.lengths[idx],
						} as IMatch)
				);
				if (mappedMatches) charMatches.push(...mappedMatches);
			});
		});

		charMatches = charMatches.sort((a, b) => a.start - b.start);

		return charMatches.reduceRight((prev: string, charMatch: IMatch, idx: number) => {
			let slice = html?.substring(charMatch.start, charMatch.end);
			slice = this.prepareTextForInjection(slice);
			switch (charMatch.classification) {
				case EMatchClassification.Human:
					slice = `<span cl-human-match cl-scan-words='${charMatch.totalWords}' cl-scan-probability='${charMatch.probability}' cl-scan-classification='${charMatch.classification}'>${slice}</span>`;
					break;
				case EMatchClassification.AI:
					slice = `<span cl-ai-match-${this.getProbabilityMatchStyle(charMatch.probability)} cl-scan-words='${
						charMatch.totalWords
					}' cl-scan-probability='${charMatch.probability}' cl-scan-classification='${
						charMatch.classification
					}'>${slice}</span>`;
					break;
				case EMatchClassification.AnotherWriter:
					slice = `<span cl-another-writer-match cl-scan-words='${charMatch.totalWords}' cl-scan-probability='${charMatch.probability}' cl-scan-classification='${charMatch.classification}'>${slice}</span>`;
					break;
				case EMatchClassification.SameWriter:
					slice = `<span cl-same-writer-match cl-scan-words='${charMatch.totalWords}' cl-scan-probability='${charMatch.probability}' cl-scan-classification='${charMatch.classification}'>${slice}</span>`;
					break;
				case EMatchClassification.Unknown:
					slice = `<span cl-unknown-match cl-scan-words='${charMatch.totalWords}' cl-scan-probability='${charMatch.probability}' cl-scan-classification='${charMatch.classification}'>${slice}</span>`;
					break;
				default:
					slice = `<span cl-no-match>${slice}</span>`;
					break;
			}
			return slice ? slice?.concat(prev) : '';
		}, '');
	}

	public getProbabilityMatchStyle(probability: number) {
		probability = probability * 100;
		if (probability < 20) return 20;
		if (probability < 30) return 30;
		if (probability < 40) return 40;
		if (probability < 50) return 50;
		if (probability < 60) return 60;
		if (probability < 70) return 70;
		if (probability < 80) return 80;
		if (probability < 90) return 90;

		return 100;
	}

	public prepareTextForInjection(text: string) {
		// replace html tags if there is any to disable injection.
		return text.replace(/</g, '&lt;').replace(/>/g, '&gt;');
	}
}

interface IMatch {
	start: number;
	end: number;
	totalWords: number;
	classification: EMatchClassification;
	probability: number;
}
