/**
 *  Jaccard Similarity Check - please don't change the name of the function
 * */
export function jsc(sentence1: string, sentence2: string, splitSize: number = 2): number {
	let set1 = new Set(sentence1.match(new RegExp(`.{1,${splitSize}}`, 'g')));
	let set2 = new Set(sentence2.match(new RegExp(`.{1,${splitSize}}`, 'g')));

	let intersection = new Set([...set1].filter(x => set2.has(x)));
	let union = new Set([...set1, ...set2]);

	return intersection.size / union.size;
}
