import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { ELocalStorageKeys } from '@ngx-common/enums/local-storage-keys.enum';
import { LocalStorageService } from '@ngx-common/services/localstorage.service';

@Component({
	selector: 'app-review-message',
	templateUrl: './review-message.component.html',
	styleUrls: ['./review-message.component.scss'],
})
export class ReviewMessageComponent implements OnInit {
	@Output() cancelReviewEmitter = new EventEmitter();
	constructor(private _localStorageService: LocalStorageService) {}

	ngOnInit(): void {}

	leaveAReview() {
		this._localStorageService.setItem(ELocalStorageKeys.ExtensionCanShowReview, false);
		let userAgent = navigator.userAgent;
		if (userAgent.match(/edg/i)) {
			window.open(
				'https://microsoftedge.microsoft.com/addons/detail/ai-content-detector-cop/phnojbhebcmipipliconmliejefcghfi',
				'_blank'
			);
		} else if (userAgent.match(/chrome|chromium|crios/i)) {
			chrome.tabs.create({
				url: 'https://chromewebstore.google.com/detail/ai-content-detector-copyl/gplcmncpklkdjiccbknjjkoidpgkcakd/reviews',
			});
		}
	}

	cancelReview() {
		// on Review cancellation update the date of the cancellation and the number of times the user canceled.
		this._localStorageService.setItem(ELocalStorageKeys.ExtensionReviewCanceledDate, new Date());
		var reviewCancelCount = this._localStorageService.getIntegerParsedItem(
			ELocalStorageKeys.ExtensionReviewCancellationCount
		);
		reviewCancelCount = reviewCancelCount + 1;
		this._localStorageService.setItem(ELocalStorageKeys.ExtensionReviewCancellationCount, reviewCancelCount);
		// if the user canceled the review request 3 times then the review should not be shown again.
		if (reviewCancelCount >= 3) {
			this._localStorageService.setItem(ELocalStorageKeys.ExtensionCanShowReview, false);
		}
		this.cancelReviewEmitter.emit();
	}
}
