import { HttpClient } from '@angular/common/http';
import { Injectable, OnInit } from '@angular/core';
import { EHumanFeedback } from '@ngx-common/enums/human-feedback.enum';
import { IAIScanHttpResult } from '@ngx-common/models/ai-scan-result';
import { environment } from '../../../../environments/environment';

@Injectable({
	providedIn: 'root',
})
export class ScanPageService implements OnInit {
	constructor(private _http: HttpClient) {}

	ngOnInit() {}

	async scanTextAsync(text: string, pi: string = null, sim: number = null) {
		var tabs = await chrome.tabs.query({ currentWindow: true, active: true });
		var pageUrl = tabs && tabs.length ? tabs[0].url : '';
		return await this._http
			.post<IAIScanHttpResult>(`${environment.extensionAPI}/v3/ai-scan`, {
				text,
				url: pageUrl.length < 2048 ? pageUrl : '',
				pi: btoa && pi ? btoa(pi) : pi,
				sim,
			})
			.toPromise();
	}

	async humanFeedbackAsync(scanId: string, feedback: EHumanFeedback) {
		try {
			await this._http
				.post(`${environment.extensionAPI}/v2/human-feedback`, {
					scanId: scanId,
					feedback: feedback,
				})
				.toPromise();
		} catch (error) {
			/* no need to handle this error */
		}
	}
}
