export interface IAIScanHttpResult {
	results: IAIScanResultItem[];
	summary: IAIScanResultSummary;
	scannedDocument: IAIScannedDocument;
	acdiToken: string;
}

export interface IAIScannedDocument {
	scanId: string;
	creationTime: string;
}

export interface IAIScanResultSummary {
	human: number;
	ai: number;
	unknown: number;
	another_writer_score: number;
}

export interface IAIScanResultItem {
	classification: EMatchClassification;
	probability: number;
	matches: IAIScanResultMatch[];
}

export interface IAIScanResultMatch {
	text: {
		chars: IAIScanResultMatchChar;
		words: IAIScanResultMatchChar;
	};
}

export interface IAIScanResultMatchChar {
	starts: number[];
	lengths: number[];
}

export enum EMatchClassification {
	Unknown = 0,
	Human = 1,
	AI = 2,
	SameWriter = 3,
	AnotherWriter = 4,
}

export const UnNeedToInitializeUrls: string[] = [`/v1/scan/ai/embedded`, `/v1/scan/embedded`];
