import { Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';

@Component({
	selector: 'app-rating-buttons',
	templateUrl: './rating-buttons.component.html',
	styleUrls: ['./rating-buttons.component.scss'],
})
export class RatingButtonsComponent implements OnInit {
	@Input() likeBtnImgSrc = 'assets/icons/ratings/thumps-up-default.svg';
	@Input() dislikeBtnImgSrc = 'assets/icons/ratings/thumps-down-default.svg';

	likeCurrentImgSrc: string;
	dislikeCurrentImgSrc: string;

	isLiked = false;
	isDisliked = false;

	@Output() like = new EventEmitter<boolean>();
	@Output() dislike = new EventEmitter<boolean>();

	constructor() {}
	ngOnInit(): void {
		this.likeCurrentImgSrc = this.likeBtnImgSrc;
		this.dislikeCurrentImgSrc = this.dislikeBtnImgSrc;
	}

	onLikePress() {
		if (this.isLiked || this.isDisliked) {
			return;
		}

		if (!this.isLiked) {
			this.isLiked = true;
			this.likeCurrentImgSrc = '../../assets/icons/ratings/thumps-up-blue-fill.svg';
			if (this.isDisliked) {
				this.isDisliked = false;
			}
			this.like.emit(true);
		} else {
			this.isLiked = false;
			this.like.emit(false);
		}
	}

	onDislikePress() {
		if (this.isLiked || this.isDisliked) {
			return;
		}

		if (!this.isDisliked) {
			this.isDisliked = true;
			this.dislikeCurrentImgSrc = '../../assets/icons/ratings/thumps-down-red-fill.svg';
			if (this.isLiked) {
				this.isLiked = false;
			}
			this.dislike.emit(true);
		} else {
			this.isDisliked = false;
			this.dislike.emit(false);
		}
	}
}
